/* ALL */
.Volumes_Volumes__2XqhC {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    margin-top: 6rem;
}

.Volumes_Wrapper__9WELA {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: #111;
    border-radius: 0.5rem;
    padding-top: 1rem;
    padding-bottom: 2rem;
    margin-bottom: 3rem;
}
.Volumes_Info__1R7fw {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-top: 1rem;
}
.Volumes_Content__pfULJ, .Volumes_Buttons__3shd7 {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
}

.Volumes_Heading__2smbK {
    color: var(--color-purple);
}

.Volumes_Name__VWd9p {
    width: 25rem;
}
.Volumes_Driver__3xJsT {
    width: 10rem;
}
.Volumes_Mountpoint__1kPh8 {
    width: 40rem;
}

.Volumes_Name__VWd9p, .Volumes_Driver__3xJsT, .Volumes_Mountpoint__1kPh8 {
    overflow: hidden;
    margin-right: 1.25rem;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.Volumes_Button__1tQmO {
    margin-right: 1.25rem !important;
}

.Volumes_Headline__3apIY {
    color: var(--color-purple);
}

.Volumes_Volumes__2XqhC h1,
.Volumes_Volumes__2XqhC h2 {
    font-weight: 500;
}

.Volumes_Volumes__2XqhC h1 {
    font-size: 1.8rem;
    margin-top: 0;
    margin-bottom: 0.8rem;
}

.Volumes_Volumes__2XqhC h2 {
    font-size: 1.6rem;
}

.Volumes_Volumes__2XqhC p {
    font-size: 1.1rem;
    text-align: center;
    margin-top: 0;
    margin-bottom: 0;
}

.Volumes_DetailTitle__2spvS {
    color: var(--color-purple);
    width: 5.5rem;
}

.Volumes_Error__2wbaX {
    color: red;
    white-space: pre;
    text-align: left !important;
}

/* desktop 1200 + */
@media (min-width: 1200px) {
    .Volumes_Name__VWd9p {
        width: 15rem;
    }
    .Volumes_Driver__3xJsT {
        width: 5rem;
    }
    .Volumes_Mountpoint__1kPh8 {
        width: 31rem;
    }

    .Volumes_Volumes__2XqhC {
        margin-top: 10rem;
    }

    .Volumes_Wrapper__9WELA {
        width: 90%;
        padding: 4.5rem 2rem 5rem 2rem;
    }
    .Volumes_Info__1R7fw {
        flex-direction: column;
        justify-content: space-around;
        margin-top: 4rem;
    }

    .Volumes_Volumes__2XqhC h1 {
        font-size: 2.5rem;
    }

    .Volumes_Volumes__2XqhC p {
        margin-bottom: 1rem;
    }
}

/* tabletLandscape 900 - 1199 */
@media (max-width: 1199px) {
    .Volumes_Driver__3xJsT {
        width: 15rem;
    }
    .Volumes_Buttons__3shd7 {
        margin-top: 1rem;
        margin-left: 1rem;
    }
    .Volumes_DetailTitle__2spvS {
        margin-left: 1.5rem !important;
    }
}

/* tabletPortrait 600 - 899 */
@media (max-width: 899px) {
    .Volumes_Mountpoint__1kPh8 {
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        word-break: break-word;
        white-space: normal;
        text-overflow: unset;
    }
    .Volumes_Buttons__3shd7 {
        flex-direction: column-reverse;
    }
    .Volumes_Button__1tQmO {
        margin-top: 0.75rem !important;
    }
    .Volumes_DetailTitle__2spvS {
        margin-left: 1.5rem !important;
    }
}

/* phone < 600 */
@media (max-width: 599px) {
    .Volumes_Name__VWd9p {
        width: 100%;
        word-break: break-word;
        margin-bottom: 1rem;
        margin-right: 0;
    }
    .Volumes_Driver__3xJsT {
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
    }
    .Volumes_DetailTitle__2spvS {
        margin-left: 0.2rem !important;
    }
}

